.container {
  display: flex;
  flex-direction: column-reverse;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: stretch;
  align-items: center;
}

.flex-item:nth-child(1) {
  order: 1;
  flex: 0 1 auto;
  align-self: auto;
}

.flex-item:nth-child(2) {
  order: 2;
  flex: 0 1 auto;
  align-self: auto;
}

.flex-item:nth-child(3) {
  order: 3;
  flex: 0 1 auto;
  align-self: auto;
}

.flex-item:nth-child(4) {
  order: 4;
  flex: 0 1 auto;
  align-self: auto;
}
